body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}


.navbar {
    padding: 10px 16px;
}

.navbar .navbar-brand {
    padding: 0;
}

.navbar .navbar-brand .logo {
    width: 40px;
    height: 40px;
}

@media (max-width: 767px) {
    .navbar-collapse {
        margin-top: 4px;
    }
}
.pwa-prompt {
    bottom: 0;
    width: 100%;
    padding: 10px;
    position: fixed;
    background-color: #2C3E50 !important;
}
.app-container {
  margin-top: 80px;
}

.preview-image-container {
    width: 270px;
    height: 200px;
    margin: 0 auto;
}

.preview-image {
    height: 100%;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    background-size: cover;
    background-position: center;
}

.preview-image-tooltip {
    top: 10px;
    right: 10px;
    width: 24px;
    height: 24px;
    display: -webkit-flex;
    display: flex;
    position: absolute;
    border-radius: 50%;
    background-size: cover;
    background-color: white;
    border: 2px solid white;
    background-position: center;
    background-image: url(data:image/svg+xml;utf8;base64,);
}

.preview-image-tooltip.active::after {
    top: 30px;
    right: 10px;
    width: 225px;
    display: -webkit-flex;
    display: flex;
    position: absolute;
    border-radius: 4px;
    content: attr(title);
    background: #303030;
    border: 1px solid #303030;
}
.pagination-controls {
    color: white;
}

.pagination-controls td {
    border-top: none;
}

.pagination-controls .page-number,
.pagination-controls .page-size {
    width: 50px;
    height: 22px;
}

.pagination-controls .icon {
    width: 14px;
    height: 14px;
    cursor: pointer;
    display: -webkit-inline-flex;
    display: inline-flex;
    background-size: contain;
    background-repeat: no-repeat;
}

.pagination-controls .icon.next {
    background-image: url("data:image/svg+xml;utf8;base64,");
}

.pagination-controls .icon.last {
    background-image: url("data:image/svg+xml;utf8;base64,");
}

.pagination-controls .icon.prev {
    background-image: url("data:image/svg+xml;utf8;base64,");
}

.pagination-controls .icon.first {
    background-image: url("data:image/svg+xml;utf8;base64,");
}

@media (max-width: 550px) {
    .pagination-compact>tbody>tr>td {
        padding-left: 4px;
        padding-right: 4px;
    }
}
.image {
  width: 200px;
  height: 150px;
  display: inline-block;
  background-size: cover;
  background-position: center;
}

.image-container{
  cursor: pointer;
}

.delete-image {
  cursor: pointer;
}

.delete-image:hover {
  opacity: 0.5;
  text-decoration: underline;
}

.th-40 {
  width: 40px;
}

.th-image {
  width: 220px;
}

@media (min-width: 1150px) {
  .pagination-compact {
    max-width: 500px;
    margin: 0 auto;
  }
}

@media (max-width: 1150px) {
  .responsive-image-table * {
    box-sizing: border-box;
  }
  .responsive-image-table *:before, .responsive-image-table *:after {
    box-sizing: border-box;
  }
  .responsive-image-table .image-table {
    border: none !important;
    background: transparent !important;
    display: block;
    box-shadow: none !important;
  }
  .responsive-image-table .image-table .image-tbody {
    display: block;
  }
  .responsive-image-table .image-table .image-thead tr:first-child {
    display: none;
  }
  .responsive-image-table .image-table .image-tbody tr {
    display: block;
    border: 1px solid #383838;
    margin-bottom: 7px;
    width: 100%;
  }
  .responsive-image-table .image-table .image-tbody td {
    display: block;
    border: none !important;
    position: relative !important;
    padding-bottom: 5px !important;
    text-align: left !important;
  }
  .responsive-image-table .image-table .image-tbody td:before {
    padding-right: 5px;
  }
  .responsive-image-table .image-table .image-tbody td {
    padding-bottom: 10px !important;
    padding-top: 7px !important;
  }
  .responsive-image-table .image-table .image-tbody .image-container {
    height: 300px;
    display: block;
  }
  .responsive-image-table .image-table .image-tbody .image-container .image {
    height: 100%;
    width: 100%;
  }
  .responsive-image-table .image-table .image-tfoot {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
  }
  .responsive-image-table .image-table .image-tfoot>tr {
    display: -webkit-inline-flex;
    display: inline-flex;
  }
  .responsive-image-table .image-table .image-tfoot>tr>td {
    border-top: none;
    display: -webkit-inline-flex;
    display: inline-flex;
  }
  .responsive-image-table .image-table .image-tfoot .table {
    margin-bottom: 0;
  }
  /* ----------------------------------------------------------
      1c. Insert Column Labels into the content attributes below (copy rows and change the td:nth-of-type numbers as needed to match table column number)
    ------------------------------------------------------------ */
  .responsive-image-table .image-table .image-tbody td:nth-of-type(1):before {
    content: "#: ";
    font-weight: bold;
  }
  .responsive-image-table .image-table .image-tbody td:nth-of-type(3):before {
    content: "Tooltip: ";
    font-weight: bold;
  }
}
.card-img-top {
    width: 275px;
    height: 240px;
    margin: 0 auto;
    margin-top: 20px;
}

.card-img-top .placeholder {
    width: 100%;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    background-size: cover;
    background-position: center;
}
